<template>
  <div class="container">
     <el-breadcrumb separator=">" class="title">
      <el-breadcrumb-item :to="{ path: '/notice' }">消息通知</el-breadcrumb-item>
    </el-breadcrumb>
    <el-button size="small" class="read-button" :type="readed ? 'primary' : 'danger'" plain>{{readed ? '全部已读' : '全部删除'}}</el-button>
    <el-tabs tab-position="left" v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="已读信息" name="first">
        <el-row v-for="notice in noticeData" :key="notice.id" class="parent">
          <el-row class="type">{{notice.type}}</el-row>
          <el-row class="content">{{notice.content}}</el-row>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="未读信息" name="second">
        <el-row v-for="notice in noticeData" :key="notice.id" class="parent">
          <el-row class="type">{{notice.type}}</el-row>
          <el-row class="content">{{notice.content}}</el-row>
        </el-row>
      </el-tab-pane>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="60">
    </el-pagination>
    </el-tabs>
  </div>
</template>

<script>
export default {
  name: 'Notice',
  data () {
    return {
      currentPage: 1,
      readed: true,
      activeName: 'first',
      noticeData: new Array(5).fill(1).map((e, i) => {
        return {
          id: i,
          type: '杂志出刊通知',
          content: '第六期杂志已出刊，请广大作者注意查收新邮件。',
        }
      })
    }
  },
  methods: {
    handleClick (tab, event) {
      this.readed = tab.$el.id === 'pane-first'
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  position: relative;
  .title{
    padding-left: 20px;
    height: 45px;
    line-height: 45px;
    border-bottom: 1px solid #ddd;
  }
  .read-button {
    position: absolute;
    right: 30px;
    top: 50px;
  }
  .el-tabs {
    margin: 20px;
  }
  .parent {
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
    .type {
      font-weight: bold;
      margin-bottom: 5px;
    }
  }
}
</style>



