<template>
  <div class="container">
    <el-breadcrumb separator=">" class="title">
      <el-breadcrumb-item :to="{ path: '/contribute' }">作者投稿</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="info">在填写信息前请确认稿件符合<a href="#">稿件模板</a>规范</div>
    <label for="addAuthor" style="margin-left:100px;">作者信息(*):</label>
    <el-button id="addAuthor" size="small" type="primary" plain>添加作者</el-button>
    <el-table
      :data="tableData"
      border
      style="width: 90%; margin: 10px 0 0 5%;">
      <el-table-column
        fixed
        prop="number"
        label="序号">
      </el-table-column>
      <el-table-column
        fixed
        prop="name"
        label="姓名">
      </el-table-column>
      <el-table-column
        prop="author"
        label="通讯作者">
      </el-table-column>
      <el-table-column
        prop="address"
        label="地址">
      </el-table-column>
      <el-table-column
        prop="company"
        label="工作单位">
      </el-table-column>
      <el-table-column
        prop="department"
        label="部门或院系"
        width="100">
      </el-table-column>
      <el-table-column
        prop="phone"
        label="手机/座机"
        width="120">
      </el-table-column>
      <el-table-column
        prop="email"
        label="E-mail"
        width="180">
      </el-table-column>
      <el-table-column
        fixed="right"
        label="删除">
        <template slot-scope="scope">
           <el-button
           @click.native.prevent="deleteRow(scope.$index, tableData4)"
           type="danger"
           icon="el-icon-delete"
           size="mini"
           circle></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-row style="margin-top: 20px;">
      <el-col :span="6">稿件标题(*):</el-col>
      <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
    </el-row>
    <el-row>
      <el-col :span="6">摘要(*):</el-col>
      <el-col :span="14"><el-input :rows="3" type="textarea" label="hehe"></el-input></el-col>
    </el-row>
    <el-row>
      <el-col :span="6">关键词(*):</el-col>
      <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
    </el-row>
    <el-row>
      <el-col :span="6">标题(英文):</el-col>
      <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
    </el-row>
    <el-row>
      <el-col :span="6">摘要(英文):</el-col>
      <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
    </el-row>
    <el-row>
      <el-col :span="6">关键词(英文):</el-col>
      <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
    </el-row>
    <el-row>
      <el-col :span="6">学科:</el-col>
      <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
    </el-row>
    <el-row>
      <el-col :span="6">计划栏目:</el-col>
        <el-select v-model="value" size="small" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
    </el-row>
    <el-row>
      <el-col :span="6">留言:</el-col>
      <el-col :span="14"><el-input :rows="4" type="textarea"></el-input></el-col>
    </el-row>
    <el-row>
      <el-col :span="6">上传文件:</el-col>
      <el-col :span="14">
        <el-upload
          class="upload-demo"
          action="https://jsonplaceholder.typicode.com/posts/"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          multiple
          :limit="3"
          :on-exceed="handleExceed"
          :file-list="fileList">
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: 'contribute',
    data () {
      return {
        options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }, {
          value: '选项4',
          label: '龙须面'
        }, {
          value: '选项5',
          label: '北京烤鸭'
        }],
        value: '',
        fileList: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}],
        tableData: [{
          number: 666,
          author: 'yes',
          name: '小冷',
          address: '杭州市',
          company: '**公司',
          department: '前端组',
          phone: '13657215665',
          email: '896054682@qq.com',
        }]
      }
    },
    methods: {
      deleteRow (index, tableData4) {
      },
      handleRemove (file, fileList) {
        console.log(file, fileList)
      },
      handlePreview (file) {
        console.log(file)
      },
      handleExceed (files, fileList) {
        this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`)
      },
      beforeRemove (file, fileList) {
        return this.$confirm(`确定移除 ${file.name}？`)
      }
    }
  }
</script>

<style lang="less" scoped>
.container {
  .title{
    padding-left: 20px;
    height: 45px;
    line-height: 45px;
    border-bottom: 1px solid #ddd;
  }
  .info {
    text-align: center;
    line-height: 50px;
  }
  .el-button.is-circle {
    padding: 5px;
  }
  .el-row {
    margin-bottom: 3px;
  }
  .el-col.el-col-6 {
    margin-right: 20px;
    text-align: right;
  }
}
</style>



