<template>
  <div class="container">
  <el-breadcrumb separator=">" class="title">
    <el-breadcrumb-item :to="{ path: '/' }">系统主页</el-breadcrumb-item>
  </el-breadcrumb>
  <el-upload
    class="avatar-uploader"
    action="https://jsonplaceholder.typicode.com/posts/"
    :show-file-list="false"
    :on-success="handleAvatarSuccess"
    :before-upload="beforeAvatarUpload">
    <img v-if="imageUrl" :src="imageUrl" class="avatar">
    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
  </el-upload>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>账户信息</span>
    </div>
    <div class="text item">
      <el-row>
        <el-col :span="6">E-mail:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">密码:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">确认密码:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">密码提示问题:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">密码提示答案:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
    </div>
  </el-card>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>用户信息</span>
    </div>
    <div class="text item">
      <el-row>
        <el-col :span="6">姓名:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">姓名(英文):</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">性别:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">出生日期:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">省份:</el-col>
        <el-col :span="14">
          <el-select size="small">
          </el-select>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">市:</el-col>
        <el-col :span="14">
          <el-select size="small">
          </el-select>
        </el-col>
      </el-row>
    </div>
  </el-card>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>通讯信息</span>
    </div>
    <div class="text item">
      <el-row>
        <el-col :span="6">手机:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">座机:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">邮政编码:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">邮寄地址:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
    </div>
  </el-card>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>研究方向</span>
    </div>
    <div class="text item">
      <el-row>
        <el-col :span="6">学科:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">研究方向:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">备注:</el-col>
        <el-col :span="14"><el-input type="textarea" :rows="4"></el-input></el-col>
      </el-row>
    </div>
  </el-card>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>教育信息</span>
    </div>
    <div class="text item">
      <el-row>
        <el-col :span="6">学历:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">院校:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">专业:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
    </div>
  </el-card>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>工作信息</span>
    </div>
    <div class="text item">
      <el-row>
        <el-col :span="6">职位:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">职称:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">单位地址:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
      <el-row>
        <el-col :span="6">单位名称:</el-col>
        <el-col :span="14"><el-input type="text" size="small" clearable></el-input></el-col>
      </el-row>
    </div>
  </el-card>
  </div>
</template>

<script>
export default {
  name: 'Account',
  data () {
    return {
      imageUrl: ''
    }
  },
  methods: {
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },
    beforeAvatarUpload (file) {
      const isLt2M = file.size / 1024 / 1024 < 4

      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 4MB!')
      }
      return isLt2M
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  .title{
    padding-left: 20px;
    height: 45px;
    line-height: 45px;
    border-bottom: 1px solid #ddd;
  }
  .avatar-uploader {
    text-align: center;
    margin-top: 20px;
    .avatar-uploader-icon {
      border: 1px dashed #292828;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
      font-size: 28px;
      color: #8c939d;
      width: 178px;
      height: 178px;
      line-height: 178px;
      text-align: center;
    }
    .avatar-uploader-icon:hover {
      border-color: #409EFF;
    }
  }
    .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
  .el-row {
    margin-bottom: 3px;
  }
  .el-col.el-col-6 {
    margin-right: 20px;
    line-height: 32px;
    text-align: right;
  }
  .box-card {
    margin: 20px;
  }
}
  
</style>





