<template>
  <div class="container">
    <el-breadcrumb separator=">" class="title">
      <el-breadcrumb-item :to="{ path: '/' }">系统主页</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>使用指南</span>
      </div>
      <div v-for="t in guide" :key="t" class="text item">
        {{ t }}
      </div>
    </el-card>
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        type="index"
        width="50">
      </el-table-column>
      <el-table-column
        prop="order"
        label="稿号"
        >
      </el-table-column>
      <el-table-column
        prop="name"
        label="标题"
        >
      </el-table-column>
      <el-table-column
        prop="author"
        label="作者"
        >
      </el-table-column>
      <el-table-column
        prop="date"
        label="投稿时间"
        >
      </el-table-column>
      <el-table-column
        prop="firstStatus"
        label="初审状态"
        >
        <template slot-scope="scope">
        <el-tag
          :type="scope.row.firstStatus === '通过' ? 'success' : 'warning'"
          close-transition>{{scope.row.firstStatus}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="payStatus"
        label="缴费状态"
        >
        <template slot-scope="scope">
        <el-tag
          :type="scope.row.payStatus === '已支付' ? 'success' : 'warning'"
          close-transition>{{scope.row.payStatus}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="finalStatus"
        label="定稿状态"
        >
        <template slot-scope="scope">
        <el-tag
          :type="scope.row.finalStatus === '已定稿' ? 'success' : 'warning'"
          close-transition>{{scope.row.finalStatus}}</el-tag>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="200">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    const messageData = [{
      order: '12345678',
      name: '如何趴着写稿',
      author: '小冷',
      date: '2018-04-23',
      firstStatus: '通过',
      payStatus: '已支付',
      finalStatus: '未定稿',
    }, {
      order: '66666666',
      name: '如何躺着写稿',
      author: '小冷',
      date: '2018-04-23',
      firstStatus: '通过',
      payStatus: '未支付',
      finalStatus: '已定稿',
    }, {
      order: '88888888',
      name: '如何坐着写稿',
      author: '小冷',
      date: '2018-04-23',
      firstStatus: '通过',
      payStatus: '已支付',
      finalStatus: '已定稿',
    }]
    const messageData2 = [{
      order: '22222222',
      name: '如何跪着写稿',
      author: '小冷',
      date: '2018-04-23',
      firstStatus: '未通过',
      payStatus: '已支付',
      finalStatus: '已定稿',
    }, {
      order: '87654321',
      name: '如何站着写稿',
      author: '小冷',
      date: '2018-04-23',
      firstStatus: '未通过',
      payStatus: '未支付',
      finalStatus: '已定稿',
    }]
    return {
      currentPage: 1,
      guide: ['（1）作者投稿前，请认真阅读投稿须知，若您在投稿过程中出现问题，可以通过顶栏上的在线客服与意见反馈与我们取得联系。',
        '（2）在系统首页您可以查看稿件的投稿状态与约稿状态，以及公告和消息等信息。',
        '（3）点击左侧功能栏的作者投稿开始投稿，在填写个人信息时，你可使用默认的信息资料或者填写新的信息资料。',
        '（4）当稿件显示审核通过时，请及时交付稿费，以确保您的稿件能按时发表。'],
      tableData: messageData.concat(messageData2),
    }
  },
  methods: {
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  .title{
    padding-left: 20px;
    height: 45px;
    line-height: 45px;
    border-bottom: 1px solid #ddd;
  }
   .item {
    margin-bottom: 12px;
    font-size: 14px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    margin: 20px;
  }

}
</style>



